@font-face {
  font-family: "Oswald Regular";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald Medium";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../source/fonts/oswaldmedium.woff") format("woff");
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}
@media (min-width: 768px) {
  .active {
    border-bottom: 2px solid #68B738;
  }
}
@media (min-width: 1280px) {
  .active--contrast {
    border-bottom: 2px solid #FFFFFF;
  }
}
.button {
  font-family: "Oswald Regular", "Roboto", sans-serif;
  background-color: #68B738;
  font-size: 16px;
  line-height: 40px;
  text-transform: uppercase;
  text-align: center;
  text-decoration: none;
  color: #FFFFFF;
  border: none;
  box-sizing: border-box;
  padding: 0 20px;
  display: inline-block;
  min-height: 40px;
  cursor: pointer;
}
.button:hover {
  background-color: #5EAA2F;
}
.button:active {
  background-color: #5EAA2F;
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .button {
    font-size: 20px;
    line-height: 40px;
  }
}
.ad-form {
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(5, auto);
}
.ad-form__fieldset {
  border: none;
  margin: 0;
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 12px;
}
.ad-form__fieldset--cat {
  padding: 0 20px 25px;
  border-bottom: 1px solid #D9D9D9;
}
.ad-form__fieldset--program {
  padding: 22px 20px 17px;
  border-bottom: 1px solid #D9D9D9;
}
.ad-form__fieldset--contacts {
  margin: 26px 0 0;
  padding: 0 20px;
  grid-gap: 14px;
}
.ad-form__fieldset--contacts::before {
  position: relative;
  content: "";
  top: -55px;
  z-index: -1;
  right: -20px;
  min-width: 100px;
  height: 2px;
  background-color: #68B738;
}
.ad-form__fieldset--comment {
  margin: 11px 0 0;
  padding: 0 20px 23px;
}
.ad-form__fieldset--comment::before {
  position: relative;
  content: "";
  top: 28px;
  z-index: -1;
  right: -20px;
  min-width: 100px;
  height: 2px;
  background-color: #68B738;
}
.ad-form__fieldset--extra {
  padding: 0 20px 32px;
}
.ad-form__fieldset--extra::before {
  position: relative;
  content: "";
  z-index: -1;
  top: -30px;
  right: -20px;
  min-width: 100px;
  height: 2px;
  background-color: #68B738;
}
.ad-form__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  position: relative;
}
.ad-form__label {
  color: #444444;
}
.ad-form__label--program {
  color: #222222;
  display: grid;
  grid-template-columns: auto 1fr;
  align-items: center;
  user-select: none;
  cursor: pointer;
}
.ad-form__label--program::before {
  content: "";
  display: flex;
  width: 30px;
  height: 30px;
  margin: 0 15px 0 0;
  border: 1px solid #CDCDCD;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 14px 14px;
}
.ad-form__label--program:hover::before {
  border: 1px solid #BABABA;
}
.ad-form__label--extra {
  color: #000000;
  display: grid;
  grid-template-columns: auto 1fr;
  align-items: center;
  user-select: none;
  cursor: pointer;
}
.ad-form__label--extra::before {
  content: "";
  display: inline-block;
  box-sizing: border-box;
  width: 30px;
  height: 30px;
  margin: 0 15px 0 0;
  border: 1px solid #CDCDCD;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 18px 14px;
}
.ad-form__label--extra:hover::before {
  border: 1px solid #BABABA;
}
.ad-form__input {
  font-family: "Oswald Regular", "Roboto", sans-serif;
  font-size: 16px;
  line-height: 45px;
  text-transform: uppercase;
  color: #444444;
  display: inline-block;
  box-sizing: border-box;
  border: 1px solid #E7E7E7;
  height: 45px;
  padding: 14px;
  margin: 0 0 0 13px;
  flex-grow: 2;
}
.ad-form__input:hover {
  border: 1px solid #CDCDCD;
}
.ad-form__input:focus {
  border-radius: 0;
  border: 2px solid #444444;
  outline: none;
}
.ad-form__input--error {
  border: 2px solid #FF8282;
}
.ad-form__icon {
  position: absolute;
  top: 14px;
}
.ad-form__icon--email {
  width: 19px;
  height: 14px;
  right: 20px;
  stroke: #222222;
}
.ad-form__icon--tel {
  width: 20px;
  height: 20px;
  right: 16px;
  fill: #000000;
}
.ad-form__input:focus + .ad-form__icon--email {
  stroke: #68B738;
}
.ad-form__input:focus + .ad-form__icon--tel {
  fill: #68B738;
}
.ad-form__input--error + .ad-form__icon--email {
  stroke: #FF8282;
}
.ad-form__input--error + .ad-form__icon--tel {
  fill: #FF8282;
}
.ad-form__radio:focus + .ad-form__label::before {
  border: 1px solid #68B738;
}
.ad-form__radio:checked + .ad-form__label--program::before {
  background-image: url("../img/common/icon_radio.svg");
}
.ad-form__radio:disabled + .ad-form__label--program {
  opacity: 0.3;
}
.ad-form__radio:disabled + .ad-form__label--program::before {
  opacity: 0.3;
}
.ad-form__checkbox:checked + .ad-form__label--extra::before {
  background-image: url("../img/common/icon_checkbox.svg");
}
.ad-form__checkbox:focus + .ad-form__label::before {
  border: 2px solid #68B738;
}
.ad-form__checkbox:disabled + .ad-form__label--extra {
  opacity: 0.3;
}
.ad-form__checkbox:disabled + .ad-form__label--extra::before {
  opacity: 0.3;
}
.ad-form__legend {
  padding: 0;
  font-size: 24px;
  line-height: 26px;
  text-transform: none;
  background-color: #FFFFFF;
  position: relative;
}
.ad-form__legend--contacts {
  width: 200px;
  margin: 0 0 18px;
}
.ad-form__legend--comment {
  width: 145px;
  margin: 0 0 24px;
}
.ad-form__legend--extra {
  width: 150px;
  margin: 0 0 20px;
}
.ad-form__textarea {
  font-family: "Oswald Regular", "Roboto", sans-serif;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: #222222;
  border: 1px solid #E7E7E7;
  border-radius: 0;
  box-sizing: border-box;
  padding: 9px 12px 9px 15px;
  height: 150px;
  resize: none;
}
.ad-form__textarea:hover {
  border: 1px solid #CDCDCD;
}
.ad-form__textarea:focus {
  border-radius: 0;
  border: 2px solid #444444;
  outline: none;
}
.ad-form__button {
  margin: 22px 0 10px;
}
.ad-form__text {
  font-family: "Arial", "Helvetica", sans-serif;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  text-transform: none;
}
@media (min-width: 768px) {
  .ad-form {
    font-size: 20px;
    line-height: 30px;
  }
  .ad-form__fieldset {
    padding: 0;
    border: none;
  }
  .ad-form__fieldset--cat {
    margin: 0 0 72px;
    grid-gap: 20px;
  }
  .ad-form__fieldset--program {
    padding: 25px 27px 22px;
    border: 1px solid #E7E7E7;
    margin: 0 0 74px;
    grid-gap: 11px;
  }
  .ad-form__fieldset--contacts {
    margin: 0 0 32px;
    grid-gap: 0;
  }
  .ad-form__fieldset--contacts::before {
    top: -90px;
    right: -90px;
  }
  .ad-form__fieldset--comment {
    margin: 0 0 65px;
  }
  .ad-form__fieldset--comment::before {
    top: 35px;
    right: -90px;
  }
  .ad-form__fieldset--extra {
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 32px 60px;
    padding: 0 0 72px;
  }
  .ad-form__fieldset--extra::before {
    top: -38px;
    grid-column: 1 / -1;
    right: -90px;
  }
  .ad-form__legend {
    font-size: 36px;
    line-height: 36px;
  }
  .ad-form__legend::before {
    position: absolute;
    content: "";
    width: 30px;
    height: 2px;
    background-color: #68B738;
    top: 23px;
    left: -70px;
  }
  .ad-form__legend--contacts {
    min-width: 540px;
    margin: 0 0 75px;
  }
  .ad-form__legend--comment {
    width: 220px;
    margin: 0 0 60px;
  }
  .ad-form__legend--extra {
    width: 240px;
    margin: 0 0 25px;
  }
  .ad-form__wrapper--contacts {
    margin: 0 0 20px;
  }
  .ad-form__input {
    height: 52px;
    font-size: 20px;
    margin: 0 0 0 19px;
    padding: 14px 17px;
  }
  .ad-form__input--email {
    background-size: 18px 13px;
    background-position: 96% center;
  }
  .ad-form__label--extra::before {
    width: 40px;
    height: 40px;
    margin: 0 19px 0 0;
    background-size: 24px 18px;
  }
  .ad-form__label--program::before {
    width: 40px;
    height: 40px;
    margin: 0 19px 0 0;
    background-size: 18px 18px;
  }
  .ad-form__icon {
    top: 19px;
  }
  .ad-form__textarea {
    font-size: 20px;
    line-height: 30px;
    padding: 10px 20px;
    height: 174px;
  }
  .ad-form__button {
    margin: 22px 0 0;
    height: 52px;
    line-height: 52px;
  }
  .ad-form__text {
    margin: 18px 0 0;
    align-self: center;
    text-align: left;
    font-size: 16px;
    line-height: 16px;
  }
}
@media (min-width: 1280px) {
  .ad-form {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(4, auto);
    grid-gap: 69px 80px;
  }
  .ad-form__fieldset--cat {
    grid-column: 1 / 2;
    margin: 0;
  }
  .ad-form__fieldset--program {
    grid-column: 2 / -1;
    margin: 0;
  }
  .ad-form__fieldset--contacts {
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 0 80px;
    grid-column: 1 / -1;
    margin: 0;
  }
  .ad-form__fieldset--contacts::before {
    grid-column: 1 / -1;
    right: -110px;
  }
  .ad-form__fieldset--comment {
    grid-column: 1 / -1;
    margin: 0;
    grid-gap: 0;
  }
  .ad-form__fieldset--comment::before {
    top: 25px;
    right: -110px;
  }
  .ad-form__fieldset--extra {
    grid-column: 1 / -1;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: auto;
    grid-gap: 0 80px;
    margin: 0;
    padding: 0 0 80px;
  }
  .ad-form__fieldset--extra::before {
    right: -110px;
    top: -70px;
  }
  .ad-form__wrapper {
    margin: 0;
  }
  .ad-form__legend::before {
    width: 60px;
    top: 23px;
    left: -110px;
  }
  .ad-form__legend--comment {
    margin: 0 0 70px;
  }
  .ad-form__legend--extra {
    margin: 0 0 57px;
  }
  .ad-form__button {
    grid-column: 1 / 3;
    margin: 61px 0 0;
  }
  .ad-form__text {
    margin: 60px 0 0;
  }
}
.card {
  margin: 0;
  padding: 19px 20px 22px;
  box-sizing: border-box;
  min-width: 280px;
  min-height: 209px;
  border-top: 1px solid #D9D9D9;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 59px 50px 40px;
}
.card__link-title {
  text-decoration: none;
  grid-column: 3 / -1;
  grid-row: 1 / 2;
}
.card__title {
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  text-transform: uppercase;
  color: #222222;
  width: 80px;
  margin: 0 0 18px;
}
.card__link-image {
  width: 140px;
  height: 112px;
  display: flex;
  justify-content: center;
  align-items: center;
  grid-column: 1 / 3;
  grid-row: 1 / 3;
}
.card__image {
  display: block;
  padding: 0;
  margin: auto;
  width: 100%;
  height: auto;
}
.card__table {
  width: 142px;
  min-height: 44px;
  margin: 0;
  padding: 0;
  grid-column: 3 / -1;
  grid-row: 2 / 3;
}
.card__property {
  text-align: left;
  margin: 0;
  padding: 0;
}
.card__value {
  text-align: right;
  margin: 0;
  padding: 0;
}
.card__row {
  font-family: "Arial", "Helvetica", sans-serif;
  font-size: 12px;
  line-height: 14px;
  color: #444444;
}
.card__button {
  grid-column: 1 / -1;
  grid-row: 3 / 4;
  margin: 15px 0 0;
  min-width: 280px;
}
@media (min-width: 768px) {
  .card {
    padding: 0 36px 39px;
    min-width: 324px;
    min-height: 511px;
    border: none;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    background-image: linear-gradient(to bottom, #FFFFFF 16%, #F2F2F2 16%);
  }
  .card__title {
    font-size: 20px;
    line-height: 24px;
    width: 150px;
    text-align: center;
    margin: 0 0 15px;
  }
  .card__image {
    margin: 40px auto 0;
  }
  .card__link-image {
    order: -1;
    align-items: center;
    width: 198px;
    height: 200px;
    margin: 0 0 42px;
  }
  .card__table {
    width: 250px;
    border-collapse: collapse;
    margin: 0 auto 33px;
  }
  .card__row {
    font-size: 16px;
    border-bottom: 1px solid #CDCDCD;
  }
  .card__property {
    padding: 7px 0 9px;
  }
  .card__value {
    padding: 7px 0 9px;
  }
  .card__button {
    min-width: 250px;
    font-size: 16px;
    margin: 0 auto;
  }
}
@media (min-width: 1280px) {
  .card {
    min-width: 245px;
    max-width: 245px;
    padding: 5px 36px 40px;
  }
  .card__image {
    margin: 0 auto;
  }
  .card__link-image {
    width: 172px;
    height: 196px;
    align-items: flex-end;
  }
  .card__table {
    width: 169px;
  }
  .card__button {
    min-width: 168px;
  }
}
.catalog {
  margin: 0;
  border-top: 1px solid #EBEBEB;
}
.catalog__title {
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  margin: 27px 20px 40px;
  grid-column: 1 / -1;
}
.catalog__wrapper {
  display: grid;
  grid-template-columns: 1fr;
  align-items: center;
}
.catalog__show-all {
  margin: 0;
  padding: 25px 20px;
  box-sizing: border-box;
  border-top: 1px solid #EBEBEB;
  border-bottom: 1px solid #EBEBEB;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  position: relative;
  height: 208px;
}
.catalog__show-all::before,
.catalog__show-all::after {
  position: absolute;
  top: 37px;
  content: "";
  width: 30px;
  height: 2px;
  background-color: #D9D9D9;
}
.catalog__show-all::after {
  transform: rotate(90deg);
}
.catalog__show-all-title {
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  text-transform: uppercase;
  color: #111111;
  margin: 53px 0 8px;
  text-align: center;
}
.catalog__show-all-text {
  font-family: "Arial", "Helvetica", sans-serif;
  font-size: 12px;
  line-height: 16px;
  color: #444444;
  margin: 0 0 21px;
  text-align: center;
}
.catalog__show-all-button {
  background-color: #F2F2F2;
  color: #444444;
  width: 100%;
}
.catalog__show-all-button:hover {
  background-color: #EBEBEB;
}
.catalog__show-all-button:active {
  color: rgba(68, 68, 68, 0.3);
}
@media (min-width: 768px) {
  .catalog {
    border: none;
    margin: 0 30px;
  }
  .catalog__wrapper {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(auto-fit, 511px);
    grid-gap: 60px;
  }
  .catalog__title {
    font-size: 60px;
    line-height: 60px;
    margin: 65px 0 82px;
  }
  .catalog__show-all {
    height: 429px;
    border: 2px solid #F2F2F2;
    padding: 0 36px 39px;
    align-self: end;
  }
  .catalog__show-all::before,
  .catalog__show-all::after {
    top: 80px;
    width: 60px;
  }
  .catalog__show-all-title {
    font-size: 20px;
    line-height: 22px;
    width: 135px;
    margin: 161px 0 23px;
    align-self: center;
  }
  .catalog__show-all-text {
    font-size: 16px;
    line-height: 18px;
    width: 170px;
    margin: 0 0 87px;
  }
  .catalog__show-all-button {
    min-width: 250px;
    font-size: 16px;
  }
}
@media (min-width: 1280px) {
  .catalog {
    margin: 0 60px;
    padding: 75px 0 0;
  }
  .catalog__wrapper {
    grid-template-columns: repeat(4, 1fr);
  }
  .catalog__title {
    margin: 0 0 84px;
  }
  .catalog__show-all {
    width: 245px;
  }
  .catalog__show-all-title {
    margin: 159px 0 23px;
  }
  .catalog__show-all-button {
    min-width: 169px;
    max-width: 169px;
  }
}
@media (min-width: 1440px) {
  .catalog {
    margin: 0 auto;
    max-width: 1220px;
  }
  .catalog__wrapper {
    grid-gap: 63px 80px;
  }
}
