.programs {
  margin: 0 20px;
  min-width: 280px;
  max-width: 700px;
}

.programs__list {
  padding: 0;
  margin: 0;
  list-style: none;
}

.programs__item {
  position: relative;
  background-color: @light-gray;
  margin: 0 0 20px;
  padding: 0 20px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  box-sizing: border-box;
  min-height: 266px;

  &:last-child {
    margin: 0;
  }
}

.programs__title {
  font-size: 24px;
  line-height: 37px;
  font-weight: 400;
  text-transform: uppercase;
  color: @basic-black;
  margin: 0;
  padding: 30px 0 31px 71px;

  &::before {
    position: absolute;
    content: "";
  }
}

.programs__title--slim::before {
  background-image: url("../img/common/cat_slim.svg");
  background-size: 50px 50px;
  top: 24px;
  left: 22px;
  height: 50px;
  width: 50px;
}

.programs__title--pro::before {
  background-image: url("../img/common/cat_pro.svg");
  background-size: 67px 50px;
  top: 24px;
  left: 13px;
  height: 50px;
  width: 67px;
}

.programs__text {
  font-family: "Arial", "Helvetica", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  line-height: 18px;
  color: @dark-gray;
  margin: 0;
  padding: 0 0 21px;
  border-bottom: 1px solid @decor-gray;
  min-width: 227px;
  grid-column: 1 / -1;
}

.programs__link {
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  color: @basic-black;
  text-decoration: none;
  cursor: pointer;
  display: block;
  box-sizing: border-box;
  width: 135px;
  padding: 10px 0;
  margin: 9px 0;
  background-image: url("../img/common/icon_arrow-short.svg");
  background-repeat: no-repeat;
  background-position: 90% 50%;
  background-size: 24px 12px;

  &:hover {
    background-image: url("../img/common/icon_arrow-long.svg");
    background-position: 97% 50%;
    background-size: 32px 12px;
  }

  &:active {
    opacity: 0.3;
  }
}

@media (min-width: @tablet-width) {
  .programs {
    margin: 0 30px;
    min-width: 708px;
    max-width: 1220px;
  }

  .programs__item {
    margin-bottom: 30px;
    padding: 77px 0 47px 53px;
    min-height: 372px;
  }

  .programs__title {
    font-size: 36px;
    line-height: 36px;
    padding: 0;
    margin: 0 0 26px;

    &::before {
      background-image: none;
    }
  }

  .programs__text {
    font-size: 16px;
    line-height: 24px;
    width: 340px;
    border-bottom: none;
    padding: 0;
    margin: 0 0 40px;
    color: @basic-black;

    &::after {
      position: absolute;
      content: "";
      background-repeat: no-repeat;
    }
  }

  .programs__text--slim::after {
    background-image: url("../img/common/cat_slim.svg");
    background-size: 200px 211px;
    top: 81px;
    right: 61px;
    width: 200px;
    height: 211px;
  }

  .programs__text--pro::after {
    background-image: url("../img/common/cat_pro.svg");
    background-size: 268px 200px;
    top: 91px;
    right: 28px;
    width: 268px;
    height: 200px;
  }

  .programs__link {
    font-size: 20px;
    line-height: 30px;
    margin: 0;
    width: 165px;
  }
}

@media (min-width: @desktop-width) {
  .programs {
    margin: 0 auto;
    max-width: 1190px;
  }

  .programs__list {
    display: flex;
    margin: 0;
    flex-direction: row;
    justify-content: space-between;
  }

  .programs__item {
    padding: 77px 52px 40px;
    margin: 0;
    min-height: 374px;
  }

  .programs__item:not(:last-child) {
    margin-right: 50px;
  }

  .programs__title--slim {
    margin: 0 0 74px 162px;

    &::before {
      background-image: url("../img/common/cat_slim.svg");
      background-size: 100px 106px;
      width: 100px;
      height: 106px;
      top: 41px;
      left: 52px;
    }
  }

  .programs__title--pro {
    margin: 0 0 72px 162px;

    &::before {
      background-image: url("../img/common/cat_pro.svg");
      background-size: 134px 100px;
      width: 134px;
      height: 100px;
      top: 47px;
      left: 33px;
    }
  }

  .programs__text {
    min-width: 467px;
    margin: 0 0 17px;

    &::after {
      background-image: none;
    }
  }
}

@media (min-width: @desktop-width-max) {
  .programs {
    max-width: 1220px;
  }

  .programs__item:not(:last-child) {
    margin-right: 80px;
  }
}
